import { Card, Container } from "@mui/material";
import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";

const Dashboard = () => {
	// const injectedHooks = Object.assign({}, ...(inject('Documentations', 'BooksList.Hooks') || []));

	const task = useTask("Customers-Club-Management.dashboard");

	return (
		<TabView {...task}>
			<ResponsiveContainer>
				<Container disableGutters>
					<Card variant="outlined" sx={{ flexGrow: 1 }}>
						داشبورد باشگاه مشتریان
					</Card>
				</Container>

				{/* {inject('Documentations', 'BooksList.Footer', { data, loading, reload, ...injectedHooks })} */}
			</ResponsiveContainer>
		</TabView>
	);
};

export default Dashboard;
